.trainee-container {
  padding: 30px;
  font-family: Arial, sans-serif;
}

.trainee-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.trainee-table th,
.trainee-table td {
  padding: 12px 15px;
  border: 1px solid #ddd;
  text-align: left;
}

.trainee-table th {
  background-color: #f2f2f2;
}

.trainee-table tr:hover {
  background-color: #f9f9f9;
}

.trainee-table button {
  padding: 6px 12px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.trainee-table button:hover {
  background-color: #45a049;
}
